 <template>
  <div class="container mx-auto px-4">
     <h2 class="uppercase font-bold text-3xl mb-4">Images</h2>
     <div class="grid grid-cols-3 md:grid-cols-5 gap-2">
      <div v-for="(image, index) in images" :key="index" >
        <img class="grid grid-cols-3 gap-4 cursor-pointer" @click="openModalImage(image)" :src="movieImage(image)" alt="">
      </div>
     </div>
  </div>
 </template>

<script>
export default {
  props: {
    images: {
      required: true
    }
  },
  methods: {
    movieImage(image){
      if(image.file_path){
        return "https://image.tmdb.org/t/p/w300/" + image.file_path
      } else {
        return "https://via.placeholder.com/300x450"
      }
    },
    openModalImage(image){
      let imageFullPath = ''
      if(image.file_path){
        imageFullPath = "https://image.tmdb.org/t/p/w1280/" + image.file_path
      } else {
         imageFullPath =  "https://via.placeholder.com/300x450"
      }

      this.$emit('onImageClick', imageFullPath)
    }
  }
  
}
</script>
