<template>
<div>
  <div class="mx-auto container px-4">
    <h2 class="uppercase font-bold text-3xl mt-8">Cast</h2>
    <div v-if="casts" class="grid grid-cols-3 md:grid-cols-4 gap-4 mt-4">
        <div v-for="cast in casts.cast" :key="cast.id">
          <img class="w-full" :src="movieImage(cast)"  alt="">
          <span>{{ cast.name }}</span>
          <div class="text-gray-600">{{cast.character}}</div>
        </div>
    </div>
  </div><br><br>
</div>
</template>

<script>
export default {
  props: {
    casts: {
      required: true
    }
  },
  methods: {
    movieImage(cast) {
      if(cast.profile_path){
        return "https://image.tmdb.org/t/p/w300/" + cast.profile_path
      } else {
        return "https://via.placeholder.com/300x450"
      }
    }
  }
  
}
</script>
