<template>
    <div :class="[modalpower ? 'opacity-10' : '']" class="flex flex-col justify-center md:flex-row container mx-auto mt-5  py-16 px-4">
    <img class="max-w-full md:w-1/3" :src="posterPath" alt="">
    <div class="md:ml-10 md:w-1/2">
      <h2 class="text-3xl font-bold mt-3">{{ movie.title}}</h2>
      <div class="flex mt-2 text-gray-500">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 text-yellow-500" viewBox="0 0 20 20" fill="currentColor">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
        <div> {{movie.vote_average}} | {{movie.release_date}}<br><span v-for="(genre, index) in movie.genres" :key="genre.id">{{ genre.name }}<span v-if="movie.genres.length -1 != index">, </span></span></div>
      </div>
      <p class="mt-5">{{ movie.overview }}</p>
      <h2 class="mt-8 text-xl font-bold">Feature Cast</h2>
      <div class="flex  mt-2">
        <div class="mr-8" v-for="(crew, index) in credits.crew" :key="index">
          <div v-if="index < 2">
            <h3 class="font-bold">{{ crew.name }}</h3>
            <span class="font-thin text-gray-500">{{ crew.job }}</span>
          </div>
        </div>
      </div>
      <div class="mt-6 flex">
        <button class="flex mr-3 px-3 py-2 rounded bg-yellow-500 hover:bg-yellow-600">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-red-500 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z" />
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
          <a class="text-gray-800 font-bold" target="_blank" @click.prevent="openModalyoutube">Play Trailer</a>
        </button>
      </div>
    </div>
  </div>
  
  <modal @updateFrame="damnIt" :modalpower="modalpower">
    <div>
      <iframe v-if="youtube"  class="w-screen rounded bg-gray-800 py-8 max-w-xl h-72 md:h-96" :src="youtubeVideo" frameborder="0" allowfullscreen></iframe>
      <img class="w-screen rounded bg-gray-800 py-8 max-w-xl h-72 md:h-96"  v-if="!youtube"  :src="popImage" alt="">
    </div>
  </modal>
  <Images :class="[modalpower ? 'opacity-10' : '']"  @onImageClick="getImage" @click="openModalImage" :images="images"/>
  <Cast :class="[modalpower ? 'opacity-10' : '']"  :casts="credits"/>

</template>

<script>
import Images from '../components/hello/Images.vue'
import Cast from '../components/hello/Cast.vue'
import axios from 'axios'
import modal from './modal.vue'
export default {
  components: {Images, Cast, modal},
  data() {
    return {
      movie: [],
      credits: [],
      images: [],
      videos: [],
      modalpower: false,
      youtube: true,
      image: true,
      popImage: '',
    }
  },
  computed: {
    posterPath() {
      if(this.movie.poster_path){
        return `https://image.tmdb.org/t/p/w1280/${this.movie.poster_path}`
      } else {
        return `https://via.placeholder.com/300x450`
      }
    },
    youtubeVideo() {
      return `https://www.youtube.com/embed/${this.videos.key}`
    }
  },
  mounted() {
    this.getDetails(this.$route.params.id)
  },
  methods: {
    async getDetails(movieId) {
      try{
        const getMovieDetails = await axios.get(`https://api.themoviedb.org/3/movie/${movieId}?api_key=fa32b020f1785f1af5227992d38677bf&append_to_response=credits,videos,images`)
        this.movie = getMovieDetails.data
        this.credits = this.movie.credits
        this.images = this.movie.images.backdrops
        this.videos = this.movie.videos.results[0]
      } catch (err) {
        console.log(err)
      }
    },
    damnIt(access) {
      this.modalpower = access
    },
    getImage(last){
      this.popImage = last
    },
    openModalImage() {
      this.modalpower = true
      this.youtube = false
      this.image = true
    },
    openModalyoutube() {
      this.modalpower = true
      this.youtube = true
      this.image = false
    }
    
  }
}
</script>

